import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Rating from "@mui/material/Rating";
import CheckboxLabels from "./check";

function createData(name, difficulty, get) {
  return { name, difficulty, get };
}

const rows = [
  createData(
    "CGクリエイター検定(ベーシック)",
    <Rating
      name="half-rating-read"
      defaultValue={1}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "CGクリエイター検定(エキスパート)",
    <Rating
      name="half-rating-read"
      defaultValue={5}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "CGエンジニア検定(ベーシック)",
    <Rating
      name="half-rating-read"
      defaultValue={2}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "CGエンジニア検定(エキスパート)",
    <Rating
      name="half-rating-read"
      defaultValue={4.5}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "Webデザイナー検定(ベーシック)",
    <Rating
      name="half-rating-read"
      defaultValue={1.5}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "Webデザイナー検定(エキスパート)",
    <Rating
      name="half-rating-read"
      defaultValue={4.5}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "画像処理エンジニア検定(ベーシック)",
    <Rating
      name="half-rating-read"
      defaultValue={2.5}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "画像処理エンジニア検定(エキスパート)",
    <Rating
      name="half-rating-read"
      defaultValue={4.5}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "マルチメディア検定(ベーシック)",
    <Rating
      name="half-rating-read"
      defaultValue={2}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  ),
  createData(
    "マルチメディア検定(エキスパート)",
    <Rating
      name="half-rating-read"
      defaultValue={4}
      precision={0.5}
      readOnly
    />,
    <CheckboxLabels />
  )
];

export default function BasicTable() {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 50 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>種類</TableCell>
            <TableCell align="left">難易度</TableCell>
            <TableCell align="left">取得</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="left">{row.difficulty}</TableCell>
              <TableCell align="left">{row.get}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
